*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  border: 0;
  // font-size: 100%;
  font-weight: normal;
  vertical-align: baseline;
  // background-position: transparent;
}

main,
article,
aside,
figure,
footer,
header,
nav,
section,
details,
summary {
  display: block;
}

//handle box-sizing on an inhereted level
html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

//this helps with img scaling, you might have an issue if you didnt make the mebed. If you do comment out height: auto;
img,
object,
embed {
  max-width: 100%;
  height: auto;
}

//add smooth scrolling to your pages
html {
  overflow-y: auto;
}

ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
  &::before,
  &::after {
    content: "";
    content: none;
  }
}

abbr[title],
dfn[title] {
  border-bottom: 1px dotted #222;
  cursor: help;
}

table {
  border-collapse: separate;
  border-spacing: 0;
}

th {
  font-weight: bold;
  vertical-align: bottom; //center is also common
}
td {
  font-weight: normal;
}

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0;
}

input,
select {
  vertical-align: middle;
}

pre {
  white-space: pre;
  white-space: pre-wrap;
  white-space: pre-line;
  word-wrap: break-word;
}

input [type="radio"] {
  vertical-align: text-bottom;
}
input [type="checkbox"] {
  vertical-align: bottom;
}
.ie7 input [type="checkbox"] {
  vertical-align: baseline;
}
.ie6 input[type="checkbox"] {
  vertical-align: text-bottom;
}

select,
input,
textarea {
  font-size: 99%;
}

small {
  font-size: 99%;
}

strong {
  font-weight: bold;
}

//super and sub text
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sup {
  top: -0.05em;
}

sub {
  bottom: -0.025em;
}

.clickable,
label,
input[type="button"],
input[type="submit"],
input[type="file"],
button {
  cursor: pointer;
}

//fix button for IE7
button,
input[type="button"] {
  width: auto;
  overflow: hidden;
}

//fix scalable images for IE7
.ie7 img {
  -ms-interpolation-mode: bicubic;
}

//fixing conatiner collapse
.clearfix::after {
  content: "";
  display: block;
  clear: both;
}
